import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/pages/index'
import index from '@/pages/index/index'//首页
import Onsaledetail from '@/pages/index/onsaledetail'
import Collocation from '@/pages/index/collocation'

Vue.use(Router)

let router= new Router({
  // mode:'history',
  routes: [
    {
      path: '/',
      // name: 'index',
      component: Index,
      children:[
        {path:'',component:index,meta:{title:'社区集市'}}
      ]
    },
    {
      path: '/onsaledetail',
      name: 'onsaledetail',
      component: Onsaledetail,
      meta:{
          title:''
      }
    },
    {
      path: '/collocation',
      name: 'collocation',
      component: Collocation,
      meta:{
          title:''
      }  
    }
    

  ]
})

////路由的钩子函数，
//在每一次路由跳转之前会进入这个方法 to：到哪去  from：从哪来 next() 调用这个方法来完成这个钩子函数
router.beforeEach((to, from, next) => {
  //动态改变title
  changeTitle(to.meta.title);
  next();
});

//动态改变title
function changeTitle(title) {
  title = title ? title : '合协社区';
  window.document.title = title;
};


export default router



